
import { ref } from "vue";
export default {
  name: "BaseStatus",
  emits: ["onclick"],
  props: {
    title: {
      type: String,
      required: true,
    },
    content: {
      type: String,
      required: true,
    },
    highlight: {
      type: Boolean,
      required: false,
    },
  },
  // eslint-disable-next-line
  setup(_: unknown, { attrs }: { attrs: any }) {
    return { clickable: ref(attrs.onclick) };
  },
};
